import React, { useState } from "react";
import styles from "./PaymentContainer.module.css";
import FixedBalance from "../FixedBalance/FixedBalance";

const PaymentContainer = () => {
  const [inputValue, setInputValue] = useState("");
  const [isValid, setValid] = useState(true);

  const ValidationDataHandler = (value) => {
    return value >= 10 && value <= 100000;
  };

  const onClickHandler = () => {
    if (isValid) {
      // Дополнительные действия при клике на кнопку (если нужно)
    }
  };

  const onChangeHandler = (event) => {
    const value = parseInt(event.target.value);
    setInputValue(event.target.value);
    setValid(ValidationDataHandler(value));
  };

  const onFocusHandler = () => {
    setValid(true);
  };

  const onBlurHandler = () => {
    const value = parseInt(inputValue);
    const isTouched = inputValue !== ""; // Проверяем, были ли изменены данные в поле

    if (isTouched && (isNaN(value) || value < 10 || value > 100000)) {
      setValid(false);
    }
    // Если данные валидны, либо поле не было изменено, оставляем класс валидности неизменным.
  };

  return (
    <section className={styles.PaymentHeader}>
      <h2>Пополнение баланса</h2>
      <section className={styles.PaymentWrapper}>
        <section>
          <FixedBalance money={50} />
          <FixedBalance money={150} />
          <FixedBalance money={500} />
        </section>
        <section>
          <section>
            <FixedBalance money={1000} />
            <FixedBalance money={2500} />
          </section>
          {!isValid && (
            <p className={styles.ErrorText}>
              Пополнение доступно от 10 до 100000
            </p>
          )}
          <section className={styles.InputYourAmount}>
            <input
              className={isValid ? styles.wrappinput : styles.errorValidation}
              placeholder="Введите сумму"
              min="10"
              max="100000"
              type="number"
              value={inputValue}
              onChange={onChangeHandler}
              onFocus={onFocusHandler}
              onBlur={onBlurHandler}
            />
            <button
              className={
                isValid ? styles.wrappbutton : styles.errorValidationButton
              }
              onClick={onClickHandler}
            />
          </section>
        </section>
      </section>
    </section>
  );
};

export default PaymentContainer;
